
import React, { Component } from 'react'
import {Table, Button, Dialog} from '@alife/next'
import {Fetch} from '../../util/request'
import {Link} from 'react-router-dom'
import dayjs from 'dayjs'
class UserList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: []
    }
    this.query = {}
    this.COLUMNS = [{
      dataIndex: 'avatarUrl',
      title: '头像',
      width: 100,
      cell: function(value) {
        return <img src={value} style={{width: '60px', height: '60px', borderRadius: '50%'}}/>
      }
    }, {
      dataIndex: 'nickName',
      title: '微信昵称'
    }, {
      dataIndex: 'realName',
      title: '姓名'
    }, {
      dataIndex: 'companyName',
      title: '公司'
    }, {
      dataIndex: 'bonus',
      title: '积分'
    }, {
      dataIndex: 'last_upload_run_time',
      title: '最近上传时间',
      cell: function(value) {
        return dayjs(new Date(value)).format('YYYY-MM-DD HH:mm')
      }
    }, {
      dataIndex: 'uid',
      title: '操作',
      cell: (value) => {
        return <div>
            <Link to={{
              pathname: '/bonusRecord',
              search: `uid=${value}`
            }}>查看积分记录</Link>
        </div>
      }
    }]
  }
  async componentDidMount() {
    this.loadList()
  }

  loadList() {
    return Fetch('getAllCustomers').then((resp) => {
      this.setState({
        data: resp.data
      })
    })
  }
  render() {
    const {data} = this.state
    return (
                <div style={{padding: '16px'}}>
                  <Table dataSource={data} >
                    {
                      this.COLUMNS.map((item, key) => {
                        return <Table.Column
                        key={key}
                        title={item.title}
                        cell={item.cell}
                        width={item.width}
                        dataIndex={item.dataIndex}
                        ></Table.Column>
                      })
                    }
                  </Table>
                </div>
    )
  }
}

export default UserList



