import React, { Component } from 'react'
import {Table, Button, Dialog} from '@alife/next'
import {Fetch} from '../../util/request'
import CodeForm from './form'
import dayjs from 'dayjs'

class Company extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: []
    }
    this.COLUMNS = [{
      title: 'id',
      dataIndex: 'cid'
    }, {
      title: '邀请码',
      dataIndex: 'code'
    }, {
      title: '公司名',
      dataIndex: 'name'
    }, {
      title: 'HR',
      dataIndex: 'hr'
    }, {
      title: 'HR联系方式',
      dataIndex: 'hr_phone'
    }, {
      title: '创建时间',
      dataIndex: 'create_time',
      cell: function(value) {
        return dayjs(new Date(value)).format('YYYY-MM-DD HH:mm')
      }
    }, {
      title: '操作',
      dataIndex: '',
      cell: (value, index, record) => {
        return <Button onClick={this.editCompany.bind(this, record)}>修改</Button>
      }
    }]
  }

  componentDidMount() {
    this.loadList()
  }
  editCompany(record) {
    const instance = Dialog.show({
      title: '修改邀请码',
      content: <CodeForm onClose={() => {
        instance.hide()
      }} data={record} onSubmit={async(data) => {
        await Fetch('editCompany', data)
        await this.loadList()
        instance.hide()
      }}></CodeForm>,
      footer: false
    })
  }
  loadList() {
    return Fetch('getAllCompany').then((resp) => {
      this.setState({
        data: resp.data
      })
    })
  }
  addCode=() => {
    const instance = Dialog.show({
      title: '新增邀请码',
      content: <CodeForm onClose={() => {
        instance.hide()
      }} onSubmit={async(data) => {
        await Fetch('addCompany', data)
        await this.loadList()
        instance.hide()
      }}></CodeForm>,
      footer: false
    })
  }
  render() {
    const {data} = this.state
    return (
            <div style={{padding: '16px'}}>
                <div style={{margin: '4px 0 8px'}}>
                  <Button type="primary" onClick={this.addCode}>新增邀请码</Button>
                </div>
                <Table dataSource={data} >
                  {
                    this.COLUMNS.map((item, key) => {
                      return <Table.Column
                      key={key}
                      title={item.title}
                      cell={item.cell}
                      dataIndex={item.dataIndex}
                      ></Table.Column>
                    })
                  }
                </Table>
            </div>
    )
  }
}

export default Company



