
import React, { Component } from 'react'
import {Table, Button, Dialog} from '@alife/next'
import {Fetch} from '../../util/request'
import {parse} from '../../util/querystring'
import dayjs from 'dayjs'
class BonusRecord extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: []
    }
    this.query = {}
    this.COLUMNS = [{
      dataIndex: 'type_name',
      title: '积分类型'
    }, {
      dataIndex: 'origin_bonus',
      title: '上一次积分'
    }, {
      dataIndex: 'add_bonus',
      title: '新增积分'
    }, {
      dataIndex: 'new_bonus',
      title: '新的积分'
    }, {
      dataIndex: 'readed',
      title: '已读',
      cell: function(value) {
        return value ? '是' : '否'
      }
    }, {
      dataIndex: 'create_time',
      title: '积分变化时间',
      cell: function(value) {
        return dayjs(new Date(value)).format('YYYY-MM-DD HH:mm')
      }
    }]
  }
  async componentDidMount() {
    this.loadList()
  }

  loadList() {
    const {location} = this.props
    const query = parse(location.search)
    return Fetch('getAllBonusInfo', query).then((resp) => {
      this.setState({
        data: resp.data
      })
    })
  }
  render() {
    const {data} = this.state
    return (
                <div style={{padding: '16px'}}>
                  <Table dataSource={data} >
                    {
                      this.COLUMNS.map((item, key) => {
                        return <Table.Column
                        key={key}
                        title={item.title}
                        cell={item.cell}
                        width={item.width}
                        dataIndex={item.dataIndex}
                        ></Table.Column>
                      })
                    }
                  </Table>
                </div>
    )
  }
}

export default BonusRecord



