
import React, { Component } from 'react'
import {Table, Button, Dialog} from '@alife/next'
import {Fetch} from '../../util/request'
import {Link} from 'react-router-dom'
import dayjs from 'dayjs'
class Share extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: []
    }
    this.query = {}
    this.COLUMNS = [{
      dataIndex: 'share_real_name',
      title: '分享者'
    }, {
      dataIndex: 'share_company_name',
      title: '分享者公司'
    }, {
      dataIndex: 'type',
      title: '分享类型',
      cell: function(val) {
        if (val === 1) {
          return '群分享'
        }
        return '未知'
      }
    }, {
      dataIndex: 'value',
      title: '分享标识'
    }, {
      dataIndex: 'create_time',
      title: '分享时间',
      cell: function(value) {
        return dayjs(new Date(value)).format('YYYY-MM-DD HH:mm')
      }
    }, {
      dataIndex: 'count',
      title: '邀请人数'
    }, {
      dataIndex: '',
      title: '操作',
      cell: (value) => {
        return <div>
            操作
        </div>
      }
    }]
  }
  async componentDidMount() {
    this.loadList()
  }

  loadList() {
    return Fetch('getShareList').then((resp) => {
      this.setState({
        data: resp.data
      })
    })
  }
  render() {
    const {data} = this.state
    return (
                <div style={{padding: '16px'}}>
                  <Table dataSource={data} >
                    {
                      this.COLUMNS.map((item, key) => {
                        return <Table.Column
                        key={key}
                        title={item.title}
                        cell={item.cell}
                        width={item.width}
                        dataIndex={item.dataIndex}
                        ></Table.Column>
                      })
                    }
                  </Table>
                </div>
    )
  }
}

export default Share



